}

.contact-icons:hover {
  font-size: 4em;
}

.cv {
  font-family: Barlow;
  font-weight: bold;
  color: #fff;
}

.tooltip {
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
}

.tooltip .tooltiptext {
  font-family: 'Barlow', sans-serif;
  font-size: 14px;
  text-align: center;
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 40%;

  /* Fade in tooltip - takes 1 second to go from 0% to 100% opac: */
  opacity: 0;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 0.9;
}

.hero{
  padding-bottom: 60px;
  display: flex;
