#steam_1{
  -webkit-animation-name: steam_1;
          animation-name: steam_1;
  -webkit-animation-delay: .2s;
          animation-delay: .2s;
  -webkit-animation-duration: 2.2s;
          animation-duration: 2.2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  position: relative;
}
#steam_2{
  -webkit-animation-name: steam_2;
          animation-name: steam_2;
  -webkit-animation-duration: 2.2s;
          animation-duration: 2.2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  position: relative;
}
#steam_3{
  -webkit-animation-name: steam_3;
          animation-name: steam_3;
  -webkit-animation-delay: .4s;
          animation-delay: .4s;
  -webkit-animation-duration: 2.2s;
          animation-duration: 2.2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  position: relative;
}

@-webkit-keyframes steam_1{
  0% {
      opacity: 0;
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
  }
  50% {
      opacity: 1;

  }
  100% {
      opacity: 0;
      -webkit-transform: translateY(-25px);
              transform: translateY(-25px);
  }
}

@keyframes steam_1{
  0% {
      opacity: 0;
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
  }
  50% {
      opacity: 1;

  }
  100% {
      opacity: 0;
      -webkit-transform: translateY(-25px);
              transform: translateY(-25px);
  }
}
